import image1 from "./images/146917.png"
import image2 from "./images/140177.png"
import React from "react"
import style from "./sentenceWrapper.module.css"
import  MediaQuery from "react-responsive"
import { useEffect } from 'react'
import gsap from "gsap"
import { ScrollTrigger } from 'gsap/ScrollTrigger'

function SentenceWrapper(){
    useEffect(()=>{
        if (process.browser) {
          gsap.registerPlugin(ScrollTrigger)
          setAnimation()
        }
      }, [])
    const setAnimation = () => {
        gsap.fromTo(
          '#sentence',
          { opacity: 0, y: 20 }, //fromの設定
          {  //toの設定
            opacity: 1,
            y: 0,
            duration: 2,
            scrollTrigger: {
              trigger: '#sentence',
              start: 'top center', //要素のトップが、画面の中央まできたら開始
              end: 'bottom center', //要素のボトムが、画面の中央まできたら終了
            },
          }
        )
    }
  
    return(
        <>
            {/* タブレット*/}
            <MediaQuery minWidth={700} maxWidth={1000}>
            <div className={style.sentence}>
                <div　id="sentence">
                    <h2>温泉は体の汚れだけでなく、心の汚れも落としてくれる</h2>
                    <p>このサイトではみなさんに日本中の温泉の情報をお届けします。
                    日本には日本３大名泉をはじめ、たくさんの温泉地があります。
                    日々の疲れを洗い流して、心身ともにリフレッシュしましょう。
                    </p>
                </div>
            <img className={style.img1} src={image1}/>
            <img className={style.img2}src={image2}/>
            </div>
            {/* PC */}
            </MediaQuery>
            <MediaQuery minWidth={1000}>
            <div　className={style.sentence}>
                <div　id="sentence">
                    <h2>温泉は体の汚れだけでなく、心の汚れも落としてくれる</h2>
                    <p>このサイトではみなさんに日本中の温泉の情報をお届けします。
                    日本には日本３大名泉をはじめ、たくさんの温泉地があります。
                    日々の疲れを洗い流して、心身ともにリフレッシュしましょう。
                    </p>
                </div>
            <img className={style.img1} src={image1}/>
            <img className={style.img2}src={image2}/>
            </div>
            </MediaQuery>
            {/* スマホ*/}
            <MediaQuery maxWidth={700}>
            <div　className={style.Ksentence}>
                <div　id="sentence">
                    <h2>温泉は体の汚れだけでなく、心の汚れも落としてくれる</h2>
                    <p>このサイトではみなさんに日本中の温泉の情報をお届けします。
                    日本には日本３大名泉をはじめ、たくさんの温泉地があります。
                    日々の疲れを洗い流して、心身ともにリフレッシュしましょう。
                    </p>
                </div>
            <img className={style.Kimg1} src={image1}/>
            <img className={style.Kimg2}src={image2}/>
            </div>
            </MediaQuery>
        </>
        
        
        
    )
}
export default SentenceWrapper