import style from "./meisen.module.css"
import OnsenItem from "./onsenIntro"
import image1 from "./images/arima1.jpg"
import image2 from "./images/kusatsu.jpg"
import image3 from "./images/gero.jpg"
import imageBack1 from "./images/137965.png"
import imageBack2 from "./images/173931.png"
import MediaQuery from "react-responsive/"
import { useEffect } from 'react'
import gsap from "gsap"
import { ScrollTrigger } from 'gsap/ScrollTrigger'

function Meisen(){
    useEffect(()=>{
        if (process.browser) {
          gsap.registerPlugin(ScrollTrigger)
          setAnimation()
        }
      }, [])
    const setAnimation = () => {
        gsap.fromTo(
          '#meisen',
          { opacity: 0, y: 20 }, //fromの設定
          {  //toの設定
            opacity: 1,
            y: 0,
            duration: 2,
            scrollTrigger: {
              trigger: '#meisen',
              start: 'top center', //要素のトップが、画面の中央まできたら開始
              end: 'bottom center', //要素のボトムが、画面の中央まできたら終了
            },
          }
        )
    }
    return(
        <>
        <MediaQuery minWidth={700} maxWidth={1000}>
            <div className={style.TmeisenWrapper}>
                <div id="meisen">
                    <h2> 日本三大名泉</h2>
                    <OnsenItem onsen="有馬温泉（兵庫県）" img={image1}>
                    茶褐色の「金泉」は塩分濃度が高く保湿効果が持続します。対照的に無色透明の「銀泉」はサラサラとした湯ざわりが特徴です。
                    </OnsenItem>
                    <OnsenItem onsen="草津温泉（群馬県）" img={image2}>
                    自然湧出量日本一を誇る草津温泉は強酸性で殺菌力が高く、「恋の病以外は治す」と言われています。
                    </OnsenItem>
                    <OnsenItem onsen="下呂温泉（岐阜県）" img={image3}>
                    アルカリ性単純泉です。なめらかな肌触りが特徴で、化粧水をつけたような美肌効果があります。
                    </OnsenItem>
                    <div className={style.clear}></div>
                </div>
                
            
                
                <img className={style.Tpinpon} src={imageBack1}/>
                <img className={style.Tyu} src={imageBack2}/>
            </div>
        </MediaQuery>
        <MediaQuery minWidth={1000}>
            <div className={style.meisenWrapper}>
                <div id="meisen">
                    <h2> 日本三大名泉</h2>
                    <OnsenItem onsen="有馬温泉（兵庫県）" img={image1}>
                    茶褐色の「金泉」は塩分濃度が高く保湿効果が持続します。対照的に無色透明の「銀泉」はサラサラとした湯ざわりが特徴です。
                    </OnsenItem>
                    <OnsenItem onsen="草津温泉（群馬県）" img={image2}>
                    自然湧出量日本一を誇る草津温泉は強酸性で殺菌力が高く、「恋の病以外は治す」と言われています。
                    </OnsenItem>
                    <OnsenItem onsen="下呂温泉（岐阜県）" img={image3}>
                    アルカリ性単純泉です。なめらかな肌触りが特徴で、化粧水をつけたような美肌効果があります。
                    </OnsenItem>
                    <div className={style.clear}></div>
                </div>
                
                <img className={style.pinpon} src={imageBack1}/>
                <img className={style.yu} src={imageBack2}/>
            </div>
        </MediaQuery>
        <MediaQuery maxWidth={700}>
            <div className={style.KmeisenWrapper}>
                <div id="meisen">
                    <h2> 日本三大名泉</h2>
                    <OnsenItem onsen="有馬温泉（兵庫県）" img={image1}>
                    茶褐色の「金泉」は塩分濃度が高く保湿効果が持続します。対照的に無色透明の「銀泉」はサラサラとした湯ざわりが特徴です。
                    </OnsenItem>
                    <OnsenItem onsen="草津温泉（群馬県）" img={image2}>
                    自然湧出量日本一を誇る草津温泉は強酸性で殺菌力が高く、「恋の病以外は治す」と言われています。
                    </OnsenItem>
                    <OnsenItem onsen="下呂温泉（岐阜県）" img={image3}>
                    アルカリ性単純泉です。なめらかな肌触りが特徴で、化粧水をつけたような美肌効果があります。
                    </OnsenItem>
                    <div className={style.clear}></div>
                </div>
                
               
            </div>
        </MediaQuery>

        
        </>
    )
}
export default Meisen